<template>

  <div id="app">
    <button @click="getAccounts">Get accounts button</button>

    <p></p>

    <table style="width:100%">
      <tr>
        <th>Account Id</th>
        <th>Account Name</th>
        <th>Account Credit_Debit_Card</th>
        <th>Account Balance</th>
        <th>Account Status</th>
        <th>Account Date of birth</th>
      </tr>
      <tr v-for="account in accounts" :key="account.accountHolder_uniqueId">
        <td>{{ account.accountHolder_uniqueId }}</td>
        <td>{{ account.accountHolder_Name }}</td>
        <td>{{ account.accountHolder_Credit_Debit_Card }}</td>
        <td>{{ account.accountHolder_Balance }}</td>
        <td>{{ account.accountHolder_Status }}</td>
        <td>{{ account.accountHolder_Dob }}</td>
      </tr>
    </table>
  </div>

</template>

<script>
import axios from 'axios'

export  default {
  name: 'App',

  data() {
    return {
      accounts: null
    }
  },
  methods: {
    async getAccounts() {
      try {
        const response = await axios.get('http://localhost:8080/api/v1/bank'); // replace with your actual API endpoint
        this.accounts = response.data;
      } catch (error) {
        console.error('Error fetching accounts:', error);
      }
    },
  }

}
</script>

<style>
table, th, td {
  border:1px solid #000000;
}
</style>